import * as axios from "axios";

const insteance = axios.create({
  withCredentials: true,
  baseURL: "https://social-network.samuraijs.com/api/1.0/",
  headers: {
    "API-KEY": "1aac8f7e-6c97-4508-b9a5-b61669322c26",
  },
});

export const usersAPI = {
  getUsers(currentPage = 1, pageSize = 10) {
    return insteance
      .get(`users?page=${currentPage}&count=${pageSize}`)
      .then((response) => response.data);
  },

  setUser(userid) {
    return insteance.get(`profile/${userid}`).then((response) => response.data);
  },

  followUser(id) {
    return insteance.post(`follow/${id}`).then((response) => response.data);
  },

  unfollowUser(id) {
    return insteance.delete(`follow/${id}`).then((response) => response.data);
  },
};

export const profileAPI = {
  getProfile(userid) {
    return insteance
      .get(`/profile/${userid}`)
      .then((response) => response.data);
  },

  getStatus(userid) {
    return insteance.get(`/profile/status/${userid}`).then((response) => {
      return response.data;
    });
  },

  updateStatus(status) {
    return insteance
      .put(`/profile/status`, {
        status,
      })
      .then((response) => {
        return response.data;
      });
  },
};

export const authAPI = {
  me() {
    return insteance.get("auth/me").then((response) => response.data);
  },

  signIn(email, password, rememberMe) {
    return insteance
      .post(`/auth/login`, {
        email,
        password,
        rememberMe
      })
      .then((response) => response.data);
  },

  signOut() {
    return insteance.delete(`auth/login`).then((response) => response.data);
  },
};
