import { profileAPI } from "./../api/api";

const ADD_POST = "ADD-POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_STATUS = "SET_STATUS";
const DELETE_POST = "DELETE_POST";

let initialState = {
  postsData: [
    { id: 1, title: "My story", message: "Hello, how are you?" },
    { id: 2, title: "The best post", message: "Test message, oh dadada" },
  ],
  profile: null,
  status: "",
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST:
      let newPost = {
        id: 5,
        title: action.title,
        message: action.message,
      };
      return {
        ...state,
        postsData: [...state.postsData, newPost],
      };
    case SET_USER_PROFILE:
      return {
        ...state,
        profile: action.profile,
      };
    case SET_STATUS:
      return {
        ...state,
        status: action.status,
      };
    case DELETE_POST:
      console.log(action)
      return {
        ...state,
        postsData: state.postsData.filter(p => p.id !== action.id),
      };
    default:
      return state;
  }
};

export const addPost = ({ postMessageContent, postMessageTitle }) => ({
  type: ADD_POST,
  title: postMessageContent,
  message: postMessageTitle,
});

export const deletePost = (postId) => ({
  type: DELETE_POST,
  id: postId,
});

export const setUserProfile = (profile) => ({
  type: SET_USER_PROFILE,
  profile,
});

export const setStatus = (status) => ({
  type: SET_STATUS,
  status,
});

/* === Thunk ===*/

export const setProfile = (userid) => {
  return async (dispatch) => {
    let data = await profileAPI.getProfile(userid);
    dispatch(setUserProfile(data));
  };
};

export const getUserStatus = (userid) => {
  return async (dispatch) => {
    let data = await profileAPI.getStatus(userid);
    dispatch(setStatus(data));
  };
};

export const updateUserStatus = (status) => {
  return async (dispatch) => {
    let data = await profileAPI.updateStatus(status);
    if (data.resultCode === 0) {
      dispatch(setStatus(status));
    }
  };
};

export default profileReducer;
