import { usersAPI } from "./../api/api";
import { updateObjectInArray } from "../components/untils/objects-helper";
const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE";
const TOTAL_COUNT = "TOTAL_COUNT";
const TOGGLE_IS_FATCHING = "TOGGLE_IS_FATCHING";
const TOGGLE_IS_FOLLOWING_PROGRESS = "TOGGLE_IS_FOLLOWING_PROGRESS";

let initialState = {
  users: [],
  pageSize: 5,
  totalItemsCount: 0,
  portionSize: 10,
  currentPage: 1,
  isFetching: true,
  followingInProgress: [],
};

const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userid, "id", {followed: true})
      };
    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userid, "id", {followed: false})
      };

    case SET_USERS:
      return {
        ...state,
        users: action.users,
      };
    case SET_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.currentPage,
      };
    case TOTAL_COUNT:
      return {
        ...state,
        totalItemsCount: action.totalCount,
      };
    case TOGGLE_IS_FATCHING:
      return {
        ...state,
        isFetching: action.isFetching,
      };
    case TOGGLE_IS_FOLLOWING_PROGRESS:
      return {
        ...state,
        followingInProgress: action.isFetching
          ? [...state.followingInProgress, action.userid]
          : state.followingInProgress.filter((id) => id != action.userid),
      };
    default:
      return state;
  }
};

export const acceptFollow = (userid) => {
  return {
    type: FOLLOW,
    userid,
  };
};

export const acceptUnfollow = (userid) => {
  return {
    type: UNFOLLOW,
    userid,
  };
};

export const setUsers = (users) => {
  return {
    type: SET_USERS,
    users,
  };
};

export const setCurrentPage = (currentPage) => {
  return {
    type: SET_CURRENT_PAGE,
    currentPage,
  };
};

export const setTotalCount = (totalCount) => {
  return {
    type: TOTAL_COUNT,
    totalCount,
  };
};

export const toggleIsFatching = (isFetching) => {
  return {
    type: TOGGLE_IS_FATCHING,
    isFetching,
  };
};

export const toggleIsFollowing = (isFetching, userid) => {
  return {
    type: TOGGLE_IS_FOLLOWING_PROGRESS,
    isFetching,
    userid,
  };
};

/* === Thunk ===*/

export const requestUsers = (currentPage, pageSize) => {
  return async (dispatch) => {
    dispatch(toggleIsFatching(true));
    let data = await usersAPI.getUsers(currentPage, pageSize);
    dispatch(toggleIsFatching(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalCount(data.totalCount));
  };
};

const followUnfollowFlow = async (dispatch, id, apiMethod, actionCreator) => {
  dispatch(toggleIsFollowing(true, id));
  let data = await apiMethod(id);
  if (data.resultCode === 0) {
    dispatch(actionCreator(id));
  }
  dispatch(toggleIsFollowing(false, id));
};

export const follow = (id) => {
  return async (dispatch) => {
    let apiMethod = usersAPI.followUser.bind(usersAPI);
    let actionCreator = acceptFollow;
    followUnfollowFlow(dispatch, id, apiMethod, actionCreator);
  };
};

export const unfollow = (id) => {
  return async (dispatch) => {
    let apiMethod = usersAPI.unfollowUser.bind(usersAPI);
    let actionCreator = acceptUnfollow;

    followUnfollowFlow(dispatch, id, apiMethod, actionCreator);
  };
};

export default usersReducer;
