import { authAPI } from "../api/api";

const USER_SIGN = "USER_SIGN";

let initialState = {
  userid: null,
};

const LoginReducer = (state = initialState, action) => {
  switch (action.type) {
    case USER_SIGN:
      return {
        ...state,
        userid: action.userid,
      };
    default:
      return state;
  }
};

const accesUserSign = (userid) => ({
  type: USER_SIGN,
  userid,
});

export const getUserLogin = (email, password) => {
  return async (dispatch) => {
    let data = await authAPI.signIn(email, password);
    if (data.resultCode === 0) {
      dispatch(accesUserSign(data.userid));
    }
  };
};

export default LoginReducer;
