import React from "react";
import style from "./friendlist.module.css";
import FriendItem from "./friendItem/friendItem";

let FriendList = ({ friendList, title }) => {
  let FriendList = friendList.map((fr) => {
    return <FriendItem name={fr.name} key={fr.id} />;
  });

  return (
    <div className={style.friendsInfo}>
      <div className={style.friendsTitle}>{title}</div>
      <div className={style.friendList}>{FriendList}</div>
    </div>
  );
};

export default FriendList;
