import { authAPI } from "./../api/api";
import { stopSubmit } from "redux-form";
const SET_USER_DATA = "auth/SET_USER_DATA";
const USER_SIGN = "auth/USER_SIGN";

let initialState = {
  id: null,
  login: null,
  email: null,
  isAuth: false,
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        ...action.data,
      };
    default:
      return state;
  }
};

export const setUserData = (id, login, email, isAuth) => ({
  type: SET_USER_DATA,
  data: { id, login, email, isAuth },
});

export const accesUserSign = (userid) => ({
  type: USER_SIGN,
  userid,
});

/* === Thunk ===*/

export const authUser = () => {
  return async (dispatch) => {
    let data = await authAPI.me();
    if (data.resultCode === 0) {
      let { id, login, email } = data.data;
      dispatch(setUserData(id, login, email, true));
    }
  };
};

export const getUserLogin = (email, password, rememberMe) => {
  return async (dispatch) => {
    let data = await authAPI.signIn(email, password, rememberMe);
    if (data.resultCode === 0) {
      dispatch(authUser());
    } else {
      let message =
        data.messages.length > 0
          ? data.messages[0]
          : "Email or password is wrong";
      dispatch(stopSubmit("login", { _error: message }));
    }
  };
};

export const getUserLogout = () => {
  return async (dispatch) => {
    let data = await authAPI.signOut();
    if (data.resultCode === 0) {
      dispatch(setUserData(null, null, null, false));
    }
  };
};

export default authReducer;
